import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

export interface MenuActions {
  name: string;
  destination: string;
  icon?: string;
  isDropdown?: boolean;
  dropdownItems?: MenuActions[];
}

@Component({
  selector: 'lib-tsae-boot-header',
  templateUrl: './tsae-boot-header.component.html',
  styleUrls: ['./tsae-boot-header.component.scss']
})
export class TsaeBootHeaderComponent {

  @Input() menuItems: MenuActions[];
  @Input() logoSrc: string;
  @Input() enableSearch: boolean;
  @Output() destinationLink = new EventEmitter();

  routeToDestination(link: string) {
    this.destinationLink.next(link);
  }

}
